<template>
  <div class="row">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width my-0 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Add product</h5>
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              @click="$router.go(-1)"
              type="primary"
              v-ripple.400
              class="flat my-0"
            >
              <i class="bx bx-x mr-1"></i>Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              class="my-0"
              type="primary"
            >
              <i class="bx bx-save mr-1"></i>Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-8 col-md-cu-8 pr-lg-0">
          <div class="box">
            <div class="flex-x-between">
              <div class="row mb-3">
                <div class="col-12 mb-3">
                  <label for="Media" class="custom-label">Media</label>
                  <file
                    :key="product.name"
                    @completed="setImage"
                    :initialData="
                      product.image ? [{ image: product.image }] : [{}]
                    "
                  />
                </div>
                <div class="col-12 mb-3">
                  <label for="Name" class="custom-label">Name</label>
                  <el-input
                    class="w-100"
                    name="Name"
                    v-model="product.name"
                  ></el-input>
                </div>

                <div class="col-lg-6 mb-3" v-if="store.type !== 'supplier'">
                  <label for="Link" class="custom-label"
                    >Price per item (MMK)</label
                  >
                  <el-input
                    class="w-100"
                    type="number"
                    v-model="product.cost_per_item"
                  ></el-input>
                </div>
                <div class="col-lg-6 mb-3">
                  <label for="Link" class="custom-label"
                    >Selling Price(MMK)</label
                  >
                  <el-input
                    class="w-100"
                    name="Sale Price(MMK)"
                    type="number"
                    v-model="product.price"
                  ></el-input>
                </div>

                <div class="col-6">
                  <label for="Link" class="custom-label">Quantity</label>
                  <el-input-number
                    :min="0"
                    class="w-100"
                    controls-position="right"
                    v-model="product.number_of_stock"
                  ></el-input-number>
                </div>

                <div class="col-lg-6">
                  <label for="Link" class="custom-label">Barcode</label>
                  <el-input class="w-100" v-model="product.barcode"></el-input>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { url, mediaUrl } from "../../../core/constant/data";
import { file } from "../../common";

export default {
  data() {
    return {
      url: url,
      mediaUrl: mediaUrl,
      product: {
        name: "",
        number_of_stock: "1",
        price: "0",
        cost_per_item: "0",
        image: "media/default.png",
        barcode: null,
      },
      saving: false,
      startEditing: false,
    };
  },
  components: { file },
  watch: {
    product: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
  },
  mounted() {
    setTimeout(() => {
      this.startEditing = false;
    }, 100);
    this.$store.commit("setIsReady", true);
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },

    setImage($event) {
      this.product.image = $event[0].image;
    },

    Post() {
      this.saving = true;
      this.store = this.store.id;
      this.$axios
        .post("product/", { data: this.product })
        .then((res) => {
          this.startEditing = false;
          this.saving = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully added.",
          });
          this.$router.push({
            name: "Edit product",
            params: { id: res.data.id },
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>

